import React from 'react';
import axios from 'axios';
import { withRouter } from "react-router-dom";
import { Redirect } from 'react-router';

class Modal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			author: '',
			text: '',
			data: []
		};

		this.handleAuthorChange = this.handleAuthorChange.bind(this);
		this.handleTextChange = this.handleTextChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);

	}

	handleAuthorChange(event) {
		this.setState({author: event.target.value});
	}

	handleTextChange(event) {
		this.setState({text: event.target.value});
	}

	async handleSubmit(event) {

		try {
	      	const response = await axios.post('http://localhost:3000' + '/api/posts', {
	          	text: this.state.text,
	          	author: this.state.author,
	        });
	    
	    } catch (error) {
	      console.log(error);
	    }

	    event.preventDefualt();

	}

	render() {
		return (
			<div class="modal fade" id="postModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
			  <div class="modal-dialog modal-dialog-centered modal-sm" role="document">
			    <div class="modal-content">
			      <div class="modal-header">
			        <h5 class="modal-title" id="exampleModalLabel">Create New Post</h5>
			        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
			          <span aria-hidden="true">&times;</span>
			        </button>
			      </div>
			      <div class="modal-body">
			        <form onSubmit={this.handleSubmit}>
			        	<Redirect to={'/'}/>
			        	<div class="form-group">
			        		<label>Name</label>
			        		<input type="text" name="author"  class="form-control" value={this.state.author} onChange={this.handleAuthorChange} />
			        	</div>
			        	<div class="form-group">
			        		<label>Text</label>
			        		<textarea value={this.state.text} onChange={this.handleTextChange} name="text" rows="4" class="form-control" maxlength="82" placeholder="82 character limit"></textarea>
			        	</div>
			        	<div class="form-group float-right">
			        		<input type="submit" value="submit" id="post" class="btn btn-primary" />
			        	</div>
			        </form>
			      </div>
			    </div>
			  </div>
			</div>
		)
	}
	
}

export default Modal;